import {  NgModule  } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

//
import { EmpresasManager } from './empresas.manager';
import { EmpresasEditor } from './empresas.editor';
import { SucursalesManager } from './sucursales.manager';
import { SucursalesEditor } from './sucursales.editor';

const routes: Routes = [
    {
        path: 'kernel/security',
        children: [
            { path: 'empresas', component: EmpresasManager },
            { path: 'empresas/:id', component: EmpresasEditor },
            { path: 'sucursales', component: SucursalesManager },
            { path: 'sucursales/:idEmpresa/:id', component: SucursalesEditor }            
        ]
    },
];

@NgModule({
    imports: [
        RouterModule.forChild(routes)
    ],
    exports: [
        RouterModule
    ]
})
export class SecurityRoutingModule { }

